<template>
    <div class="project-wraper">
        <div class="main-content__title"></div>
        <div class="project-path-info">httphttp<%= helloworld %>/dist/hello.js<%=
            helloworld %>helloworld
        </div>
        <el-form label-width="100px">
            <template v-for="(obj ,index) in $dc.redirectPathVariableArray">
                <div class="project-path">
                    <div class="left">
                        <el-form-item label="">
                            <el-input v-model="obj.key" placeholder=""/>
                        </el-form-item>
                        <el-form-item label="">
                            <el-input v-model="obj.value" placeholder=""/>
                        </el-form-item>
                    </div>
                    <div class="right">
                        <el-button
                                class="delete-btn"
                                type="danger"
                                icon='el-icon-delete'
                                size="mini"
                                @click='deleteParam(obj,index)'
                        />
                    </div>
                </div>
            </template>

            <el-form-item>
                <el-button @click="addParam"></el-button>
                <el-button type="primary" @click="saveFile"></el-button>
            </el-form-item>
        </el-form>
    </div>
</template>

<script>
  import profileApi from '../../../api/profile';
  import './project.pcss'
  import forEach from 'lodash/forEach';

  export default {
    name: 'project-path',

    methods: {
      deleteParam(row, index, list) {
        this.$confirm(', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          this.$dc.redirectPathVariableArray.splice(index, 1);
        });
      },

      async saveFile() {
        var redirectPathVariableMap = {};
        forEach(this.$dc.redirectPathVariableArray, obj => {
          redirectPathVariableMap[obj.key] = obj.value;
        });
        this.$dc.profile.redirectPathVariables = redirectPathVariableMap;
        let response = await profileApi.saveFile(this.$dc.profile);
        let serverData = response.data;
        if (serverData.code == 0) {
          this.$message({
            showClose: true,
            type: 'success',
            message: '!'
          });
        } else {
          this.$message.error(`${serverData.msg}`);
        }
      },

      addParam() {
        this.$dc.redirectPathVariableArray.push({
          key: '',
          value: ''
        });
      }
    }
  };
</script>



